
<template>
    <main style="margin:auto">
      <!-- <van-loading type="gradient-circle" color="black" /> -->
      <zxhwfooter :active='1'></zxhwfooter>
    </main>
</template>

<script>
import "lib-flexible"
import zxhwfooter from '../components/zxhwfooter.vue'
import { Toast } from 'vant';
export default{
  name:'loading',
  components: {
    zxhwfooter
  },
  data(){
      return {

      }
  },
  beforeCreate () {
    const toast = Toast.loading({
      duration: 0,       // 持续展示 toast
      forbidClick: true, // 禁用背景点击
      message: '正在加载中....',
      mask: true
});
  },
  created () {
    let _this = this;
    _this.onToYH();
  },
	methods:{
    onToYH(){

      let _this = this;
      _this.$get('guanjia_heard/user/login/yhsdLogingUrl').then((res)=>{
        console.log({res})
          if(res.data.code==200){
              window.location.href = res.data.yhsdloginUrl;
              Toast.clear();
            ;
          }
        }
      )}
	}
}
</script>



